$main_color: #19acc3;

* {
  margin: 0;
  padding: 0;
}
/*body, #root, .main {*/
/*  height: 100%;*/
/*}*/
#root {
  padding: 10px;
  box-sizing: border-box;
}
.main {
  background-color: $main_color;
  border-radius: 70px;
  padding: 18px 18px 18px 64px;
  box-sizing: border-box;
  display: flex;
  height: calc(100vh - 24px);
}

.navigation {
  width: 224px;
  padding-top: 60px;
  &__icon {
    margin-right: 30px;
  }
  & ul {
    list-style-type: none;
  }
  &__title_wrapper {
    display: flex;
    padding: 0 40px;
    border-bottom-left-radius: 20px;
    border-top-left-radius: 20px;
    height: 60px;
    align-items: center;
    margin-left: -18px;
    background-color: inherit;
    color: #fff;
  }
  &__icon {
    color: #fff;
  }

  &__active_link {
    background: #fff;
    & .navigation__icon {
      color: $main_color;
    }
    & .navigation__title_wrapper {
      color: $main_color;
    }
  }
  &__base_link {
   display: flex;
    flex-direction: column;
    text-decoration: none;
    cursor: pointer;
  }
}
.top_radial_devider, .bottom_radial_devider {
  height: 15px;
  background-color: $main_color;
}
.top_radial_devider {
  border-bottom-right-radius: 20px;
}
.bottom_radial_devider {
  border-top-right-radius: 20px;
}
.nav_header {
  margin-bottom: 100px;
  &__title {
    font-size: 30px;
    color: #fff;
    font-family: sans-serif;
  }
}

.content {
  flex: 1;
  background-color: #fff;
  border-radius: 54px;
  padding: 60px 60px 60px 90px;
}
